import * as THREE from 'three'
import { OrbitControls } from 'three/addons/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { ShadowMesh } from 'three/addons/objects/ShadowMesh.js'
import { Pane } from 'tweakpane';
import * as EssentialsPlugin from '@tweakpane/plugin-essentials';
import { sin, positionLocal, time, vec2, vec3, vec4, uv, uniform, color, rangeFog } from 'three'

/**
 * Base
 */
const pane = new Pane();
pane.registerPlugin(EssentialsPlugin);
const fpsGraph = pane.addBlade({
    view: 'fpsgraph',
    label: 'fpsgraph',
})

const gltfLoader = new GLTFLoader()

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
const fogColor = uniform(color('#1b191f'))
const fogNode = rangeFog(fogColor, 20, 50)
scene.fogNode = fogNode

// Ground plane for shadows
const groundGeometry = new THREE.PlaneGeometry(20, 20)
const groundMaterial = new THREE.MeshStandardMaterial({ 
    color: '#aaaaaa', // Lighter color for better shadow contrast
    roughness: 0.8,
    metalness: 0.2
})
const ground = new THREE.Mesh(groundGeometry, groundMaterial)
ground.rotation.x = -Math.PI * 0.5
ground.position.y = 0 // Set to 0 to match the plane constant
scene.add(ground)

// Light for better visibility
const ambientLight = new THREE.AmbientLight(0xffffff, 0.5)
scene.add(ambientLight)

const directionalLight = new THREE.DirectionalLight(0xffffff, 2)
directionalLight.position.set(5, 7, -1)
directionalLight.lookAt(scene.position)
scene.add(directionalLight)

// Light visualization (helper arrows)
const arrowDirection = new THREE.Vector3()
arrowDirection.subVectors(scene.position, directionalLight.position).normalize()

const arrowPosition = new THREE.Vector3().copy(directionalLight.position)
const arrowHelper = new THREE.ArrowHelper(
    arrowDirection, 
    arrowPosition, 
    0.9, 
    0xffff00, 
    0.25, 
    0.08
)
scene.add(arrowHelper)

// ShadowMesh parameters
const normalVector = new THREE.Vector3(0, 1, 0)
const planeConstant = 0 // Ground plane is at y=0
const groundPlane = new THREE.Plane(normalVector, planeConstant)
const lightPosition4D = new THREE.Vector4()

lightPosition4D.x = directionalLight.position.x
lightPosition4D.y = directionalLight.position.y
lightPosition4D.z = directionalLight.position.z
// amount of light-ray divergence: 0.001 = sunlight, 1.0 = pointlight
lightPosition4D.w = 0.1 // Increased slightly to make shadow more visible

/**
 * Chair model and shadow
 */
let chair
let chairShadow
gltfLoader.load('/chair.glb', (gltf) => {
    chair = gltf.scene
    
    // Position and scale adjustments if needed
    chair.position.set(0, 1, 0) // Raise chair above ground
    chair.scale.set(1, 1, 1)
    scene.add(chair)
    
    // Create shadow for the chair with custom material for better visibility
    chairShadow = new ShadowMesh(chair)
    // Make shadow darker and more visible
    chairShadow.material.opacity = 0.9
    scene.add(chairShadow)
    
    // Log to help debug
    console.log("Chair loaded", chair)
    console.log("Shadow created", chairShadow)
    
    // Add to GUI
    const chairFolder = pane.addFolder({ title: 'Chair Position' })
    
    chairFolder.addInput(chair.position, 'x', { min: -5, max: 5, step: 0.1 })
    chairFolder.addInput(chair.position, 'y', { min: -1, max: 5, step: 0.1 })
    chairFolder.addInput(chair.position, 'z', { min: -5, max: 5, step: 0.1 })
    
    const chairRotationFolder = pane.addFolder({ title: 'Chair Rotation' })
    
    chairRotationFolder.addInput(chair.rotation, 'y', { 
        min: -Math.PI, 
        max: Math.PI, 
        step: 0.01,
        label: 'Rotate'
    })
})

/**
 * Light controls
 */
const lightFolder = pane.addFolder({ title: 'Light Settings' })

// Light type control
const lightParams = { 
    useDirectionalLight: true,
    intensity: 2
}

// Light type toggle
lightFolder.addInput(lightParams, 'useDirectionalLight', { label: 'Directional Light' })
    .on('change', () => {
        if (lightParams.useDirectionalLight) {
            // Directional light settings
            lightPosition4D.w = 0.001
            directionalLight.position.set(5, 7, -1)
        } else {
            // Point light settings
            lightPosition4D.w = 0.9
            directionalLight.position.set(0, 6, -2)
        }
        
        // Update light position
        lightPosition4D.x = directionalLight.position.x
        lightPosition4D.y = directionalLight.position.y
        lightPosition4D.z = directionalLight.position.z
        
        // Update arrow helper
        arrowDirection.subVectors(scene.position, directionalLight.position).normalize()
        arrowPosition.copy(directionalLight.position)
        arrowHelper.position.copy(arrowPosition)
        arrowHelper.setDirection(arrowDirection)
    })

// Light intensity
lightFolder.addInput(lightParams, 'intensity', { min: 0, max: 5, step: 0.1 })
    .on('change', () => {
        directionalLight.intensity = lightParams.intensity
    })

// Light position controls
lightFolder.addInput(directionalLight.position, 'x', { min: -10, max: 10, step: 0.1 })
    .on('change', () => {
        updateLightPosition()
    })

lightFolder.addInput(directionalLight.position, 'y', { min: 1, max: 10, step: 0.1 })
    .on('change', () => {
        updateLightPosition()
    })

lightFolder.addInput(directionalLight.position, 'z', { min: -10, max: 10, step: 0.1 })
    .on('change', () => {
        updateLightPosition()
    })

function updateLightPosition() {
    // Update light position 4D vector
    lightPosition4D.x = directionalLight.position.x
    lightPosition4D.y = directionalLight.position.y
    lightPosition4D.z = directionalLight.position.z
    
    // Update arrow helper
    arrowDirection.subVectors(scene.position, directionalLight.position).normalize()
    arrowPosition.copy(directionalLight.position)
    arrowHelper.position.copy(arrowPosition)
    arrowHelper.setDirection(arrowDirection)
}

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () => {
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight
    
    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()
    
    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(25, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 6
camera.position.y = 3
camera.position.z = 10
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGPURenderer({
    canvas: canvas,
    forceWebGL: false
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setClearColor(fogColor.value)

/**
 *  Background Color GUI
 */
const params = { color: '#1b191f' };
// add a folder for the scene background color
const folder = pane.addFolder({ title: 'Background Color' });
folder.addInput(params, 'color').on('change', () => {
    const newColor = new THREE.Color(params.color);
    renderer.setClearColor(newColor);
    fogColor.value = newColor;
});

/**
 * Animate
 */
const tick = () => {
    fpsGraph.begin()
    
    // Update controls
    controls.update()
    
    // Update shadow if chair exists
    if (chair && chairShadow) {
        // Update light position before updating shadow (in case it was changed via GUI)
        lightPosition4D.x = directionalLight.position.x
        lightPosition4D.y = directionalLight.position.y
        lightPosition4D.z = directionalLight.position.z
        
        // Update shadow with current parameters
        chairShadow.update(groundPlane, lightPosition4D)
        
        // Alternative debugging approach - create shadow each frame if not working
        // scene.remove(chairShadow)
        // chairShadow = new ShadowMesh(chair)
        // chairShadow.material.opacity = 0.9
        // scene.add(chairShadow)
        // chairShadow.update(groundPlane, lightPosition4D)
    }
    
    // Render
    renderer.renderAsync(scene, camera)
    
    fpsGraph.end()
}

renderer.setAnimationLoop(tick)